<template>
  <v-container fill-height>
    <v-row justify="center" align="center">
      <v-col lg="6">
        <v-card>
          <v-card-text>
            <v-container>
              <form @submit.prevent="newUser">
                <v-row row>
                  <v-col xs12>
                    <v-text-field
                      name="username"
                      label="Username"
                      id="username"
                      v-model="username"
                      type="username"
                      :rules="usernameRules"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-text-field
                      name="email"
                      label="Mail"
                      id="email"
                      v-model="email"
                      type="email"
                      :rules="emailRules"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-text-field
                      name="password"
                      label="Password"
                      id="password"
                      v-model="password"
                      type="password"
                      :rules="passwordRules"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-text-field
                      name="confirmPassword"
                      label="Confirm Password"
                      id="confirmPassword"
                      v-model="confirmPassword"
                      type="password"
                      required
                      :rules="[comparePasswords]"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row row>
                  <v-col xs12>
                    <v-btn type="submit">Sign up</v-btn>
                  </v-col>
                </v-row>
              </form>
            </v-container>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      username: "",
      usernameRules: [
        v => !!v || "Username is required",
        v => (v && v.length >= 4) || "Username must be more than 4 characters"
      ],
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      password: "",
      passwordRules: [
        v => !!v || "Password is required",
        v => (v && v.length >= 6) || "Password must be more than 6 characters"
      ],
      confirmPassword: ""
    };
  },
  methods: {
    ...mapActions(["register"]),
    newUser() {
      this.register({
        username: this.username,
        email: this.email,
        password: this.password
      })
        .then(() => this.$router.push("/"))
        .catch(err => console.log(err));
    }
  },
  computed: {
    comparePasswords() {
      return () =>
        this.password === this.confirmPassword || "Password must match";
    }
  }
};
</script>